<template>
    <div class="timeSelect">
        <div class="top">
          <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle">选择时间</span>
            <span class="back"></span>
            <span class="nextStep" @click="next">下一步</span>
          </div>
          <div class="dateSection">
            <ul class="week">
              <li v-for="(day, index) in days" :key="index">{{ day }}</li>
            </ul>
            <ul class="date">
              <li ref="dates" v-for="(date, index) in dateNum" :key="index" @click="selectDate(index)">{{ date.split('-')[1] }}</li>
              <span class="previous" @click="previousWeek">
                <img src="../../assets/img/next.png">
              </span>
              <span class="next" @click="nextweek">
                <img src="../../assets/img/next.png">
              </span>
            </ul>
            <div class="month">
              <span>{{ month }}</span>
              <span>月</span>
            </div>
          </div>
          <div class="notice">确定全天上课时间段，连续时间段视为一节课</div>
        </div>
        <ul class="times">
            <li class="time" v-for="(time, index) in times" :key="index">
                <span class="teachTime">{{ time.time }}</span>
                <span class="selectBox" @click="selectTime (index)">
                    <img :src="time.checked ? checked : unchecked">
                </span>
            </li>
        </ul>
        <ul class="lessonType">
            <li v-for="(lessonType, index) in lessonTypes" :key="index" :class="{ active: lessonType.isSelected }" @click="selectType(index)">{{ lessonType.type }}</li>
        </ul>
    </div>
</template>

<script>
import { submitPersonalLesson, checkLesson, deleteDayLesson } from '../../api/getdata'
import { Toast, Indicator, MessageBox } from 'mint-ui'
import { merge } from '../../../static/js/merge'
import store from '../../vuex/store'
export default {
  name: 'timeSelect',
  data () {
    return {
      active: 'active',
      checked: require('../../assets/img/checked.png'),
      unchecked: require('../../assets/img/unchecked.png'),
      month: new Date().getMonth() + 1,
      days: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
      dateNum: [],
      thisWeek: [],
      nextWeek: [],
      storeLessons: [],
      lessonTypes: [{
        type: '普课', isSelected: true
      }, {
        type: '私教', isSelected: false
      }],
      times: [{
        time: '6:00-6:30', checked: false, lessonType: ''
      }, {
        time: '6:30-7:00', checked: false, lessonType: ''
      }, {
        time: '7:00-7:30', checked: false, lessonType: ''
      }, {
        time: '7:30-8:00', checked: false, lessonType: ''
      }, {
        time: '8:00-8:30', checked: false, lessonType: ''
      }, {
        time: '8:30-9:00', checked: false, lessonType: ''
      }, {
        time: '9:00-9:30', checked: false, lessonType: ''
      }, {
        time: '9:30-10:00', checked: false, lessonType: ''
      }, {
        time: '10:00-10:30', checked: false, lessonType: ''
      }, {
        time: '10:30-11:00', checked: false, lessonType: ''
      }, {
        time: '11:00-11:30', checked: false, lessonType: ''
      }, {
        time: '11:30-12:00', checked: false, lessonType: ''
      }, {
        time: '12:00-12:30', checked: false, lessonType: ''
      }, {
        time: '12:30-13:00', checked: false, lessonType: ''
      }, {
        time: '13:00-13:30', checked: false, lessonType: ''
      }, {
        time: '13:30-14:00', checked: false, lessonType: ''
      }, {
        time: '14:00-14:30', checked: false, lessonType: ''
      }, {
        time: '14:30-15:00', checked: false, lessonType: ''
      }, {
        time: '15:00-15:30', checked: false, lessonType: ''
      }, {
        time: '15:30-16:00', checked: false, lessonType: ''
      }, {
        time: '16:00-16:30', checked: false, lessonType: ''
      }, {
        time: '16:30-17:00', checked: false, lessonType: ''
      }, {
        time: '17:00-17:30', checked: false, lessonType: ''
      }, {
        time: '17:30-18:00', checked: false, lessonType: ''
      }, {
        time: '18:00-18:30', checked: false, lessonType: ''
      }, {
        time: '18:30-19:00', checked: false, lessonType: ''
      }, {
        time: '19:00-19:30', checked: false, lessonType: ''
      }, {
        time: '19:30-20:00', checked: false, lessonType: ''
      }, {
        time: '20:00-20:30', checked: false, lessonType: ''
      }, {
        time: '20:30-21:00', checked: false, lessonType: ''
      }, {
        time: '21:00-21:30', checked: false, lessonType: ''
      }, {
        time: '21:30-22:00', checked: false, lessonType: ''
      }]
    }
  },
  mounted () {
    function getWeek (i) {
      var now = new Date()
      var firstDay = new Date(now - (now.getDay() - 1) * 86400000)
      firstDay.setDate(firstDay.getDate() + i)
      var oMonth = Number(firstDay.getMonth() + 1)
      return oMonth + '-' + firstDay.getDate()
    }
    for (let j = 0; j < 7; j++) {
      this.thisWeek.push(getWeek(j))
    }
    this.dateNum = this.thisWeek
    for (let k = 7; k < 14; k++) {
      this.nextWeek.push(getWeek(k))
    }
    console.log(this.dateNum)
    store.commit('setLessonType', '普课')
    window.scrollTo(100, 0)
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    next () {
      for (var k = 0; k < store.state.normalLesson.length; k++) {
        this.storeLessons.push({time: store.state.normalLesson[k], state: false})
      }
      store.commit('saveLessons', this.storeLessons)
      // console.log(store.state.normalLesson.length)
      if (store.state.lessonDate !== '' & store.state.normalLesson.length !== 0 || store.state.personalLesson.length !== 0) {
        // console.log(store.state.personalLesson)
        // console.log(store.state.lessonDate)
        let lessonDate = new Date().getFullYear() + '-' + store.state.lessonDate
        let hasLesson = checkLesson({date: lessonDate})
        hasLesson.then(res => {
          console.log(res)
          if (res.status === '0') {
            MessageBox.confirm('', {
              message: '当日已有课程,确定重新编辑？'
            }).then(action => {
              if (action === 'confirm') {
                let deleteData = deleteDayLesson({date: lessonDate})
                deleteData.then(res => {
                  console.log(res)
                })
                let data = []
                for (var i = 0; i < store.state.personalLesson.length; i++) {
                  data.push({date: lessonDate, tiems: store.state.personalLesson[i]})
                }
                if (data.length !== 0) {
                  Indicator.open({
                    text: '正在生成私教课',
                    spinnerType: 'snake'
                  })
                  submitPersonalLesson(data).then(res => {
                    console.log(res)
                    if (res.status === '0') {
                      Indicator.close()
                      store.commit('savePersonalLesson', [])
                      Toast({
                        message: '已生成私教课程',
                        duration: 1500
                      })
                      if (store.state.normalLesson.length !== 0) {
                        this.$router.push('/home/mine/lessonSelect')
                      } else {
                        this.$router.push('/home/mine/myLesson')
                      }
                    }
                  })
                } else if (data.length === 0 & store.state.normalLesson.length !== 0) {
                  this.$router.push('/home/mine/lessonSelect')
                }
              }
            })
          } else {
            let data = []
            for (var i = 0; i < store.state.personalLesson.length; i++) {
              data.push({date: lessonDate, tiems: store.state.personalLesson[i]})
            }
            if (data.length !== 0) {
              Indicator.open({
                text: '正在生成私教课',
                spinnerType: 'snake'
              })
              submitPersonalLesson(data).then(res => {
                console.log(res)
                if (res.status === '0') {
                  Indicator.close()
                  store.commit('savePersonalLesson', [])
                  Toast({
                    message: '已生成私教课程',
                    duration: 1500
                  })
                  if (store.state.normalLesson.length !== 0) {
                    this.$router.push('/home/mine/lessonSelect')
                  } else {
                    this.$router.push('/home/mine/myLesson')
                  }
                } else if (res.status === '-1' || res.info === '请设置私教价格!') {
                  // console.log('2')
                  Indicator.close()
                  MessageBox.confirm('', {
                    message: '请先设置私教价格'
                  }).then(action => {
                    if (action === 'confirm') {
                      this.$router.push('/home/mine/lessonPrice')
                    }
                  })
                } else {
                  Indicator.close()
                  Toast({
                    message: '私教课发布失败，请稍后再试',
                    duration: 1500
                  })
                }
              })
            } else if (data.length === 0 & store.state.normalLesson.length !== 0) {
              this.$router.push('/home/mine/lessonSelect')
            }
          }
        })
      } else {
        Toast({
          message: '请先选择课程时间',
          duration: 1500
        })
      }
    },
    nextweek () {
      this.dateNum = this.nextWeek
      for (var i = 0; i < this.$refs.dates.length; i++) {
        if (this.$refs.dates[i].classList.contains('selected')) {
          this.$refs.dates[i].classList.remove('selected')
        }
      }
    },
    previousWeek () {
      this.dateNum = this.thisWeek
      for (var i = 0; i < this.$refs.dates.length; i++) {
        if (this.$refs.dates[i].classList.contains('selected')) {
          this.$refs.dates[i].classList.remove('selected')
        }
      }
    },
    selectTime (index) {
      this.times[index].checked = !this.times[index].checked
      this.times[index].lessonType = store.state.lessonType
      var timeArr = []
      // console.log(this.times[index].checked)
      for (var i = 0; i < this.times.length; i++) {
        if (this.times[i].checked === true) {
          timeArr.push(this.times[i])
        }
      }
      // console.log(timeArr)
      var normalLesson = []
      var personalLesson = []
      for (var j = 0; j < timeArr.length; j++) {
        if (timeArr[j].lessonType === '普课') {
          normalLesson.push(timeArr[j].time)
        } else {
          personalLesson.push(timeArr[j].time)
        }
      }
      store.commit('saveNormalLesson', merge(normalLesson))
      store.commit('savePersonalLesson', merge(personalLesson))
      // console.log('普课：' + store.state.normalLesson)
      // console.log('私教：' + store.state.personalLesson)
    },
    selectType (index) {
      for (var i = 0; i < this.lessonTypes.length; i++) {
        this.lessonTypes[i].isSelected = false
        this.lessonTypes[index].isSelected = true
        store.commit('setLessonType', this.lessonTypes[index].type)
      }
      // console.log(store.state.lessonType)
    },
    selectDate (index) {
      for (var i = 0; i < this.$refs.dates.length; i++) {
        this.$refs.dates[i].classList.remove('selected')
        this.$refs.dates[index].classList.add('selected')
      }
      store.commit('setLessonDate', this.dateNum[index])
      console.log(store.state.lessonDate)
    }
  }
}
</script>

<style scoped>
 * {
    margin: 0;
    padding: 0;
    font-size: 0.48rem;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    font-size: 0.48rem;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
  }

  .nextStep {
    position: absolute;
    right: 0;
    font-size: 0.42667rem;
  }

  .top {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
    z-index: 999;
  }

  .dateSection {
    width: calc(100% - 2rem);
    margin-left: 2rem;
    position: relative;
  }

  .date {
    display: flex;
    list-style: none;
    color: #333333;
    margin-top: 0.35rem;
    position: relative;
  }

  .week {
    display: flex;
    list-style: none;
    color: #666666;
  }

  .week li {
    margin: 0 0.15rem;
    width: 0.69333rem;
    text-align: center;
    font-size: 0.34667rem;
  }

  .date li {
    margin: 0 0.15rem;
    width: 0.69333rem;
    text-align: center;
    font-size: 0.4rem;
    position: relative;
  }

  .month {
    position: absolute;
    left: -1.6rem;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #ffd200;
  }

  .previous,
  .next {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 0.21132rem;
    height: 0.4rem;
  }

  .next {
    right: 0.5rem;
    top: 0.08rem;
  }

  .previous {
    left: -0.5rem;
    top: 0.08rem;
    transform: rotate(180deg);
  }

  .notice {
    width: 100%;
    height: 0.9rem;
    background: #ffe6c8;
    font-size: 0.37333rem;
    color: #666666;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 0.2rem;
  }

  .times {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    margin-top: 4.1rem;
    list-style: none;
  }

  .time {
    height: 1.35rem;
    border-bottom: 1px solid #f5f5f5;
    display: flex;
    align-items: center;
  }

  .teachTime {
    display: inline-block;
    width: 2.4rem;
    text-align: center;
    font-size: 0.37333rem;
    color: #666666;
  }

  .selectBox {
    display: inline-block;
    border-radius: 50%;
    width: 0.6rem;
    height: 0.6rem;
    margin-left: 1rem;
  }

  .lessonType {
    position: fixed;
    right: 0;
    top: 4.2rem;
    list-style: none;
  }

  .lessonType li {
    width: 1.86667rem;
    height: 1.04rem;
    font-size: 0.42667rem;
    background: #f5f5f5;
    color: #c0c0c0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-top-left-radius: 0.52rem;
    border-bottom-left-radius: 0.52rem;
    margin-top: 0.61333rem;
  }

  .lessonType li.active {
    background: #ffd200;
    color: #fff;
  }

  .selected::before {
    content: '';
    width: 0.74rem;
    height: 0.74rem;
    border-radius: 50%;
    position: absolute;
    left: calc(50% - 0.37rem);
    top: calc(50% - 0.37rem);
    background: #ffd200;
    z-index: -1;
  }
</style>



